ashboard-selector .dashboard-card .dash-title{border-bottom:1px solid #ccc;display:flex;align-items:center;color:#444;padding:15px;margin:0}.dashboard-selector .dashboard-card .dash-description{color:#777;text-align:left;padding:15px;margin:0}section.chart{border-top:1px solid #ccc;padding-bottom:15px;margin-left:5px;margin-right:5px;min-width:350px;margin-bottom:20px;float:left}section.chart .chart-header-container{color:#333;display:flex;text-align:left;justify-content:space-between;padding:10px 20px}section.chart .chart-header-container i{cursor:pointer;font-size:20px}section.chart .chart-header-container i:hover{color:#2d8cf0}section.chart .options-container{position:relative}section.chart div.chart{width:100%;height:100%}

