nav.responsive-toolbar {
  background: $carboncopy;
  height: 60px;
  width: 100%;
  position: fixed;
  box-shadow: 1px 2px 2px 1px $framing;
  z-index: 9;

  & ~ * {
    padding-top: 60px;
  }

  ul {
    background: $carboncopy;
    display: flex;
    margin-block-start: 0;
    margin-block-end: 0;
    padding-inline-start: 0;
    height: 100%;
    align-items: center;
    box-shadow: 1px 2px 2px 1px $framing;

    figure {
      margin-block-start: 0;
      margin-block-end: 0;
      margin-inline-start: 2px;
      margin-inline-end: 0;
      cursor: pointer;
    }

    li {
      list-style-type: none;
      padding: 10px 20px;

      .active {
        background: #999;
      }
    }

    a {
      color: $background;
      text-decoration: none;
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
    }

    i {
      margin-right: 5px;
      font-size: 20px;
    }
  }
}

@media screen and (max-width: 759px) {
  nav.responsive-toolbar {
    & ~ * {
      margin-left: 60px;
    }

    ul {
      height: 100%;
      width: 300px;
      left: -240px;
      position: fixed;
      top: 60px;
      flex-direction: column;
      transition: 300ms ease all;

      &.active {
        left: 0px;
      }

      figure {
        position: fixed;
        left: 9px;
        top: 10px;
      }

      li {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
      }

      a {
        flex-direction: row;
        justify-content: space-between;
        margin-left: 20px;
        margin-right: 16px;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  nav.responsive-toolbar {
    & ~ * {
      margin-left: 0;
    }
    ul {
      left: -300px;
    }
  }
}
